<template>
    <div>
       <SlideXRightTransition>
            <div id="download_button" v-show="view" :class="{full: full}">        
                <i class="fas fa-cloud-download-alt loader"></i>
                <span class="text">{{msg_computed}}</span>
            </div>
        </SlideXRightTransition>
    </div>
</template>
<script>
import { setTimeout } from 'timers';
import { SlideXRightTransition } from 'vue2-transitions'

export default {
    components:{
        SlideXRightTransition
    },
    data: () => ({
        msg:"Verificando",
        view: false,
        full: false
    }),
    computed:{
        msg_computed(){
            var msg = this.msg;
            if(this.$store.state.pwa == "ready"){
                this.view = true;
                msg = "Downloading...";
                
            }

            else if(this.$store.state.pwa == "registered"){
                this.full = true;
                this.view = true;
                msg = "Cached :D";

                setTimeout( () => {
                    this.view = false;
                }, 3000)
            }

            else if(this.$store.state.pwa == "offline"){
                this.full = true;
                this.view = true;
                msg = "Modo Offline";

                // setTimeout( () => {
                //     this.view = false;
                // }, 3000)
            }
            
            //Default
            // else{
            //     this.view = true;
            //     this.msg = "Verificando";
            // }

            return msg;
        }
    }
}
</script>


<style lang="scss">


#download_button {
  border-radius: 4px;
    color: white;
    cursor: pointer;
    display: inline-block;
    font-weight: bold;
    padding: 15px;
    position: relative;
    background: #009994;
    position: fixed;
    bottom: 28px;
    right: 24px;
}

#download_button:hover {
  opacity: .8;
}

#download_button span.text {
  float: left;
  line-height: 24px;
}


#download_button::after {
  background: #FFF;
  border-radius: 4px;
  content: " ";
  display: block;
  height: 4px;
  position: absolute;
  bottom: 5px;
    width: 81%;
  animation-duration: 60s;
  animation-name: slidein;
  animation-iteration-count: 1;

}
#download_button.full::after{
    animation-name: unset !important;
}

@keyframes slidein {
  0% {
    width: 0%;
  }
  10% {
    width: 20%;
  }
  20% {
    width: 40%;
  }
  50% {
    width: 75%;
  }
  100% {
    width: 100%;
  }
  
}

.loader {
  width: 24px;
  height: 24px;
  display:block;
  float: left;
  margin-right: 8px;  
}
</style>
