import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    pwa: "",
    view_slide: true
  },
  mutations: {
    "update-pwa"(state, pwa) {
			state.pwa = pwa;
		},
    "update-view-slide"(state, view_slide) {
			state.view_slide = view_slide;
		}
  },
  actions: {

  }
})
