<template>
    <div class="idal_images">    
        <slide-x-right-transition>
            <div class="idal_image1" v-show="image == 'image2'">
                <img src="@/assets/idal_3g_max.png" class="bg_image" >      
            </div>
        </slide-x-right-transition>
        <slide-x-right-transition>
            <div class="idal_image1" v-show="image == 'image1'">
                <img src="@/assets/idal_3g_max2.png" class="bg_image" >      
            </div>
        </slide-x-right-transition>
        <!-- <div class="idal_image2">
            <img src="@/assets/idal_3g.png" class="bg_image">      
        </div> -->
        
    </div>
    
</template>
<script>
import { SlideXRightTransition } from 'vue2-transitions'
export default {
    data: () => ({
        image: ""
    }),
    components:{
        SlideXRightTransition
    },
     created(){
         var index = 1;

        setTimeout(() => {
        this.image = "image1";
        }, 1000)

        setInterval(() => {

            this.image = "image" + index;

            index++;
            if(index > 2){
                index = 1;
            }
        }, 5000)


  }
         
}
</script>

<style lang="scss">
    .idal_image1{
        position: absolute;
        top: 110px;
        right: 160px;
        width: 430px;
        overflow: hidden;

        img{
            transform:translateX(100px);
            // animation: show_image 1s ease-in;
            // opacity:0.4;
            
        }
    }
</style>
